.footer-nav {
  display: flex;
  margin: 0 85px;
}

.footer-item {
  display: flex;
  flex: 1;
  div {
    margin-right: 30px;
    margin-top: 40px;
    margin-bottom: 40px;
    width: 255px;
    a {
      text-decoration: none;
      color: #121212;
      opacity: 0.8;
    }
  }
}

.contact {
  margin-top: 40px;
  a {
    display: flex;
    margin-bottom: 21px;
    text-decoration: none;
    font-family: Spartan;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 13px;
    color: #121212;
    opacity: 0.8;
    img {
      position: relative;
      bottom: 3px;
      margin-right: 11px;
    }
  }
}

h2 {
  font-family: Spartan;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
}

ul {
  list-style: none;
  padding-left: 0;
  li {
    font-family: Spartan;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 34px;
    color: #121212;
    opacity: 1;
  }
}
